form {
  display: flex;
  flex-direction: column;
  width: 200px;
  gap: 20px;
  outline: 1px solid black;
  padding: 20px;
}

button, ul, h2, p {
  padding: 0;
  margin: 0;
  list-style: none;
}

h2 {
  margin-bottom: 10px;
  margin-top: 10px;
}

input { 
  width: 97%;
}

body {
  padding-top: 20px;
  padding-left: 20px;
}

li {
  margin-left: 15px;
}

ul {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

#root > label {
  display: block;
  width: 200px;
  margin-bottom: 15px;
}